<template>
	<view class="singer-vocalConcert">
		<view @click="goback" class="arrow iconfont iconback"></view>
		<!-- #ifdef MP-WEIXIN -->
		<button class="share" open-type="share"></button>
		<!-- #endif -->
		<!-- #ifdef APP-PLUS -->
		<view class="share"></view>
		<!-- #endif -->
		<view class="top">
			<view class="bg1"></view>
			<view class="bg2"></view>
			<text class="t1">ALL-CHINA POP STAR CONCERT</text>
			<view class="bg-list row rowCenter">
				<view class="bg-a"></view>
				<view class="bg-b"></view>
				<view class="bg-c"></view>
			</view>
			<view class="wrap">
				<!-- <view class="btn">点击抢票免费看音乐会</view> -->
				<view class="media">
					<uni-swiper-dot :info="activeStarInfo" :current="current" :mode="mode">
						<swiper class="swiper-box" @change="change">
							<swiper-item v-for="(item, index) in activeStarInfo" :key="index">
								<view class="swiper-item"><video :src="item.url" :id="item.id" v-if="autoplay[index]" autoplay="true"  loop="true"></video></view>
							</swiper-item>
						</swiper>
					</uni-swiper-dot>
				</view>
				<view class="appointment" v-if="activeDetail.active_detail_data">
					<view class="appointment-btn" @click="reserve">{{ is_reserve_text }}</view>
					<view class="row time-box rowCenter verCenter">
						<text class="time">{{ String(activeCountdown.d).length > 1 ? String(activeCountdown.d).substring(0, 1) : 0 }}</text>
						<text class="time">{{ String(activeCountdown.d).length > 1 ? String(activeCountdown.d).substring(1, 2) : String(activeCountdown.d).substring(0, 1) }}</text>
						<text class="text">天</text>
						<text class="time">{{ String(activeCountdown.h).length > 1 ? String(activeCountdown.h).substring(0, 1) : 0 }}</text>
						<text class="time">{{ String(activeCountdown.h).length > 1 ? String(activeCountdown.h).substring(1, 2) : String(activeCountdown.h).substring(0, 1) }}</text>
						<text class="text">时</text>
						<text class="time">{{ String(activeCountdown.m).length > 1 ? String(activeCountdown.m).substring(0, 1) : 0 }}</text>
						<text class="time">{{ String(activeCountdown.m).length > 1 ? String(activeCountdown.m).substring(1, 2) : String(activeCountdown.m).substring(0, 1) }}</text>
						<text class="text">分</text>
					</view>
					<text class="appointment-text">已有 {{ activeDetail.active_detail_data.num }} 人预约</text>
				</view>
			</view>
		</view>
		<view class="section">
			<view class="section-title" v-if="activeDetail.active_detail_data">
				<view class="t1">{{ activeDetail.active_detail_data.start_time }}</view>
				<view class="t2">{{ activeDetail.active_detail_data.address }}</view>
			</view>
			<view class="section-one">
				<view class="nav row avarage">
					<text @click="tab(index)" :class="{ active: active == index }" v-for="(item, index) in itemtext" :key="index">{{ item }}</text>
				</view>
				<view class="edit-text" v-if="activeDetail.active_detail_data">
					<text class="t1">活动介绍</text>
					<view class="t2"><u-parse mode="aspectFill" :imageProp="imageProp" :content="activeDetail.active_detail_data.active_details" @preview="preview" @navigate="navigate"></u-parse></view>
				</view>
			</view>
			<view class="section-two" v-if="activeDetail.song_list">
				<view class="title row bothSide verCenter">
					<view class="column">
						<text class="t1">歌曲投票</text>
						<text class="t2">每人每天可投票6首歌曲。</text>
					</view>
					<view class="more row verCenter" v-if="activeDetail.song_list.length >= 6" @click="more = !more">
						<text class="t3">更多</text>
						<text class="iconfont iconjiantou"></text>
					</view>
				</view>
				<template v-if="activeDetail.song_list.length > 0">
					<view class="singer">
						<view class="box row bothSide" v-for="(item, index) in activeDetail.song_list" :key="index">
							<template v-if="index < 6">
								<view class="votes-wrap">
									<text class="t1">{{ index + 1 }}.《{{ item.name }}》</text>
									<view class="votes">
										<text :style="{ width: (item.vote_num / activeDetail.upperLimit) * 100 + '%' }">{{ item.vote_num }}票</text>
									</view>
								</view>
								<view class="status" @click="toggle(index, item.id)"><text class="default" :class="{ active: song_list_status[index] }"></text></view>
							</template>
							<template v-else>
								<template v-if="more">
									<view class="votes-wrap">
										<text class="t1">{{ index + 1 }}.《{{ item.name }}》</text>
										<view class="votes">
											<text :style="{ width: (item.vote_num / activeDetail.upperLimit) * 100 + '%' }">{{ item.vote_num }}票</text>
										</view>
									</view>
									<view class="status" @click="toggle(index, item.id)"><text class="default" :class="{ active: song_list_status[index] }"></text></view>
								</template>
							</template>
						</view>
					</view>
					<view class="btn" @click="songVote">确认投票</view>
				</template>
				<template v-else>
					<view class="no-date column rowCenter">
						<view class="bg"></view>
						<text>暂无歌曲投票</text>
					</view>
				</template>
			</view>
			<view class="section-three">
				<view class="title row bothSide verCenter">
					<view class="column"><text class="t1">留言互动</text></view>
					<navigator class="operation" :url="'/pages/message/publish?aid=' + aid"></navigator>
				</view>
				<view class="list">
					<template v-if="commentGetList.comment_data.length > 0">
						<view class="box row" v-for="(item, index) in commentGetList.comment_data" :key="index">
							<view class="pic"><image :src="item.avatar" mode="aspectFill" lazy-load="true"></image></view>
							<view class="text">
								<navigator :url="'/pages/message/reply?comment_id=' + item.id + '&aid=' + aid" hover-class="none">
									<text class="t1">{{ item.name }}</text>
									<text class="t2">{{ item.add_time }}</text>
									<text class="t3">{{ item.msg }}</text>
								</navigator>
								<view class="list-media row">
									<view class="box" v-for="(v, indexs) in item.picture_and_vedio">
										<template v-if="v.type == 1">
											<image :src="v.url" mode="aspectFill" lazy-load="true" @click="previewPic(item.picture_and_vedio, indexs)"></image>
										</template>
										<template v-else-if="v.type == 2">
											<video :src="v.url"></video>
										</template>
									</view>
								</view>
								<view class="ico-bar row">
									<navigator class="row verCenter mr1" :url="'/pages/message/reply?comment_id=' + item.id + '&aid=' + aid" hover-class="none">
										<text class="ico-1"></text>
										<text class="ico-bar-txt">{{ item.reply_num }}</text>
									</navigator>
									<view class="row verCenter mr2" @click="commentUserLike(item.id, index)">
										<text :class="item.is_like == 1 ? 'curr-ico' : 'ico-2'"></text>
										<text class="ico-bar-txt">{{ item.likes }}</text>
									</view>
									<view class="row verCenter">
										<!-- #ifdef MP-WEIXIN -->
										<button class="ico-3" open-type="share" id="1" :data-commentid="item.id" :data-value="item.msg"></button>
										<!-- #endif -->
										<!-- #ifdef APP-PLUS -->
										<text class="share"></text>
										<!-- #endif -->
									</view>
								</view>
							</view>
						</view>
						<navigator :url="'/pages/message/messagList?aid=' + aid" hover-class="none" class="total">全部{{ commentGetList.total }}条留言</navigator>
					</template>
					<template v-else>
						<view class="no-date column rowCenter">
							<view class="bg"></view>
							<text>暂无留言</text>
						</view>
					</template>
				</view>
			</view>
			<view class="section-four" v-if="activeDetail.invitation_star">
				<view class="title row bothSide verCenter">
					<view class="column">
						<text class="t1">拟邀明星</text>
						<text class="t2">推荐你喜欢的6个明星</text>
					</view>
					<view class="more row verCenter" v-if="activeDetail.invitation_star.length >= 6" @click="mores = !mores">
						<text class="t3">更多</text>
						<text class="iconfont iconjiantou"></text>
					</view>
				</view>
				<view class="list row">
					<template v-if="activeDetail.invitation_star.length > 0">
						<template v-for="(item, index) in activeDetail.invitation_star">
							<template v-if="index < 6">
								<view class="box" @click="starVote(item.id)">
									<view class="pic"><image lazy-load="true" fade-show="true" :src="item.p_image" mode="aspectFill"></image></view>
									<view class="text">
										<text class="t1">{{ item.name }}</text>
										<text class="t1">{{ item.vote_num }}票</text>
									</view>
								</view>
							</template>
							<template v-else>
								<template v-if="mores">
									<view class="box" @click="starVote(item.id)">
										<view class="pic"><image lazy-load="true" fade-show="true" :src="item.p_image" mode="aspectFill"></image></view>
										<view class="text">
											<text class="t1">{{ item.name }}</text>
											<text class="t1">{{ item.vote_num }}票</text>
										</view>
									</view>
								</template>
							</template>
						</template>
					</template>
					<template v-else>
						<view class="no-date column rowCenter">
							<view class="bg"></view>
							<text>暂无拟邀明星</text>
						</view>
					</template>
				</view>
			</view>
			<view class="section-five" v-if="activeDetail.more_active">
				<view class="title">
					<text class="t1">更多活动</text>
					<text class="t2">每星期一场音乐会</text>
				</view>
				<template v-if="activeDetail.more_active.length > 0">
					<view class="list">
						<view @click="update(item.id)" class="box row verCenter" v-for="(item, index) in activeDetail.more_active" :key="index">
							<view class="pic"><image :src="item.picture_and_vedio[0].url" mode="aspectFill" lazy-load="true" fade-show="true"></image></view>
							<view class="text">
								<text class="t1">{{ item.title }}</text>
								<text class="t2">{{ item.add_time }}</text>
								<text class="t2">{{ item.address }}</text>
								<view class="t3 row">
									<text v-for="v in item.star_list">{{ v }}</text>
								</view>
								<view class="t4">
									<text class="t4a">¥</text>
									<text class="t4a">{{ item.price }}</text>
									<text class="t4c">起</text>
								</view>
							</view>
						</view>
					</view>
					<view class="toal">全部{{ activeDetail.activeTotal }}场明星音乐会</view>
				</template>
				<template v-else>
					<view class="no-date column rowCenter">
						<view class="bg"></view>
						<text>暂无更多活动</text>
					</view>
				</template>
			</view>
		</view>
		<view class="shade-box" v-if="status">
			<view class="success column verCenter">
				<view class="wrap">
					<text class="ico-1"></text>
					<text class="ico-2"></text>
					<text class="t1">预约成功</text>
					<text class="t2">{{ reserveObj.msg }}</text>
					<text class="t3">{{ reserveObj.start_time }}</text>
				</view>
				<!-- #ifdef MP-WEIXIN -->
				<button class="btn" open-type="share">立即分享</button>
				<!-- #endif -->
				<!-- #ifdef APP-PLUS -->
				<view class="btn">立即分享</view>
				<!-- #endif -->
				<view class="close" @click="status = false"></view>
			</view>
		</view>
	</view>
</template>

<script>
import uniSwiperDot from '@/components/uni-swiper-dot/uni-swiper-dot.vue';
import uParse from '@/components/u-parse/u-parse.vue';
import { API } from '@/util/api.js';

export default {
	data() {
		return {
			autoplay: [true],
			activeStarInfo: [],
			activeDetail: {},
			is_reserve_text: '立即预约',
			active: 0,
			aid: '',
			star_id: 5,
			reserveObj: {},
			activeCountdown: '',
			itemtext: ['活动详情', '歌曲投票', '留言互动', '拟邀明星', '更多活动'],
			current: 0,
			mode: 'round',
			song_list: [],
			song_list_status: [],
			flag: false,
			more: false,
			mores: false,
			status: false,
			commentGetList: {},
			activeName:'',
			imageProp: {
				padding: 0,
				lazyLoad: true,
				mode: 'aspectFill'
			}
		};
	},
	onLoad(options) {
		console.log(options);
		this.aid = options.id || options.aid;
	},
	onShow() {
		let loginRes = this.checkLogin('/pages/singer/vocalConcert', '1', { aid: this.aid });

		if (loginRes) {
			this.getData();
			this.getActiveName();
		}
	},
	onShareAppMessage(res) {
		if (res.from === 'button') {
			
		}
		if (res.target.id == 1) {
			return {
				title: this.activeName,
				path: '/pages/message/share?comment_id='+res.target.dataset.commentid
			};
		} else {
			return {
				title: '分享好友一起去看演唱会',
				path: '/pages/singer/vocalConcert?id=' + this.aid
			};
		}
	},
	methods: {
		previewPic(arr, index) {
			let imgArr = [];
			if (arr) {
				for (let i = 0; i < arr.length; i++) {
					if (arr[i].type == 1) {
						imgArr.push(arr[i].url);
					}
				}
			}
			uni.previewImage({
				urls: imgArr,
				current: index
			});
		},
		preview(src, e) {},
		navigate(href, e) {},
		getActiveName(){
			this.request(API.getActiveName, 'GET', { aid: this.aid }).then(res => {
				if (res.code === 10000) {
					this.activeName=res.data.name;
				}
			});
		},
		change(e) {
			this.current = e.detail.current;
			this.star_id = this.activeStarInfo[e.detail.current].id;

			let trailer = this.activeStarInfo;
			for (let i = 0; i < trailer.length; i++) {
				let temp = trailer[i].id;

				if (temp == this.star_id) {
					this.$set(this.autoplay, i, true);
				} else {
					this.$set(this.autoplay, i, false);
				}

				this.$forceUpdate();
			}

			this.getDetail(); //用户滑动则更新数据
		},
		toggle(index, id) {
			this.$set(this.song_list_status, index, !this.song_list_status[index]);
		},
		tab(index) {
			this.active = index;
			let arr = ['.section-one', '.section-two', '.section-three', '.section-four', '.section-five'];

			uni.createSelectorQuery()
				.select('.singer-vocalConcert')
				.boundingClientRect(data => {
					uni.createSelectorQuery()
						.select(arr[index])
						.boundingClientRect(res => {
							uni.pageScrollTo({
								duration: 100,
								scrollTop: res.top - data.top
							});
						})
						.exec();
				})
				.exec();
		},
		goback() {
			// uni.navigateBack({
			// 	delta: 1
			// });
			uni.switchTab({
			    url: '/pages/index/index'
			});
		},
		getData() {
			//获取倒计时
			this.request(API.countdown, 'GET', { aid: this.aid }).then(res => {
				if (res.code === 10000) {
					this.activeCountdown = res.data;
				}
			});

			//明星数据
			this.request(API.activeStarInfo, 'GET', { aid: this.aid },true).then(res => {
				if (res.code === 10000) {
					this.activeStarInfo = res.data;
					this.star_id = res.data[0].id; //取默认详情数据
					this.getDetail();
				}
			});

			//获取评论列表
			this.request(API.commentGetList, 'GET', { aid: this.aid, page: 1, limit: 2 }).then(res => {
				if (res.code === 10000) {
					this.commentGetList = res.data;
				}
			});
		},
		getDetail() {
			//音乐会详情页
			this.request(API.activeDetail, 'GET', { aid: this.aid, star_id: this.star_id }).then(res => {
				if (res.code === 10000) {
					this.activeDetail = res.data;
					if (res.data.song_list.length > 0) {
						this.song_list_status = [];
						for (let i = 0; i < res.data.song_list.length; i++) {
							this.song_list_status.push(false);
						}
					}
				}
			});
		},
		reserve() {
			this.request(API.reserve, 'GET', { aid: this.aid, star_id: this.star_id }).then(res => {
				if (res.code === 10000) {
					this.reserveObj = res.data;
					if (res.data.is_reserve === 1) {
						this.is_reserve_text = '已预约';
					}
					this.status = true;
				} else {
					uni.showToast({ title: res.msg, icon: 'none' });
				}
			}, true);
		},
		starVote(star_id) {
			this.request(API.starVote, 'GET', { aid: this.aid, star_id: star_id }).then(res => {
				if (res.code === 10000) {
					uni.showToast({
						title: '投票成功'
					});
					setTimeout(() => {
						this.getData();
					}, 1500);
				} else {
					uni.showToast({ title: res.msg, icon: 'none' });
				}
			}, true);
		},
		commentUserLike(id, index) {
			this.request(API.commentUserLike, 'GET', { comment_id: id }).then(res => {
				if (res.code === 10000) {
					this.$set(this.commentGetList.comment_data[index], `likes`, this.commentGetList.comment_data[index].likes * 1 + 1); //本地更新点赞数据
					this.$set(this.commentGetList.comment_data[index], `is_like`, 1);
					uni.showToast({
						title: '点赞成功'
					});
				} else {
					uni.showToast({ title: res.msg, icon: 'none' });
				}
			}, true);
		},
		songVote() {
			this.song_list = [];
			let result = (result = this.song_list_status.filter(item => item == true));

			if (result.length <= 0) {
				uni.showModal({
					title: '提示',
					content: '请选择歌曲',
					showCancel: false
				});
				return false;
			}

			if (result.length > 6) {
				uni.showModal({
					title: '提示',
					content: '每人每天可投票6首歌曲',
					showCancel: false
				});
				return false;
			}

			let arr = (result = this.song_list_status.filter((item, index, value) => {
				if (item == true) {
					this.song_list.push(this.activeDetail.song_list[index].id);
				}
			}));

			this.request(API.songVote, 'POST', { aid: this.aid, star_id: this.star_id, song_list: this.song_list }).then(res => {
				if (res.code === 10000) {
					uni.showToast({ title: res.msg, icon: 'success' });
					setTimeout(() => {
						this.song_list_status = [];
						this.getData();
					}, 1500);
				} else {
					uni.showToast({ title: res.msg, icon: 'none' });
				}
			});
		},
		update(id) {
			this.aid = id;
			this.getData();
			uni.pageScrollTo({
				duration: 100,
				scrollTop: 0
			});
		}
	},
	components: {
		uniSwiperDot,
		uParse
	}
};
</script>

<style lang="scss">
@import '../../assets/css/singer/vocalConcert.scss';
</style>
